import React, {useEffect, useState} from 'react'
import {useParams} from 'react-router-dom'
import {projectFirestore} from '../Firebase/config'


function UserDetails(props) {
    
    const {id} = useParams()
    const [state, setstate] = useState([])
    const [userDetails, setuserDetails] = useState()
    let results = []

    useEffect(() => {
     projectFirestore.collection('posts').onSnapshot(snap => {
         snap.docs.forEach(doc => {
            results.push({id: doc.id, data: doc.data()})
         })
          setstate(results)
     })
    }, [])
    

    const filtered = state.filter(state => {
        return state.data.id == id
    }) 
    
    const images = filtered.map(data => {
     return  data.data.post
    })

    const handleFollow = () => {
      if (filtered && props.user) {
       const follower = {
         name: props.user.displayName,
         id: props.user.uid,
         photo: props.user.photoURL
       }
      }
    }
    
    return (
        <div>
           
          {filtered.length > 0 &&   <div className="about-user">
              <img src={filtered[0].data.profile} className="profile-image" alt="" />
             <div className="user-activity">
             <h2>{filtered[0].data.name}</h2>
              <div className="details">
              <h3>0 followers</h3>
              <h3> {images.length} posts</h3>
              </div>
            {props.user.uid !== filtered[0].data.id &&   <button onClick={handleFollow}>Follow</button>}
             </div>
          </div>}

          <div className="main-parent">
          {filtered.length > 0 && <div className="user-container">
             {images.map(img => {
                return <img className="user-images" src={img} alt="" />
             })}
               </div>}
          </div>
              
            
        </div>
    )
}

export default UserDetails
