import React, {useState, useRef, useEffect} from 'react'
import {useParams} from 'react-router-dom'
import '../Styles/Comments.css'
import {projectFirestore} from '../Firebase/config'

function Comments(props) {

    const {id} = useParams()
    const commentsRef = useRef()
    const [comments, setcomments] = useState([])

    useEffect(() => {
        projectFirestore.collection('posts').doc(id).onSnapshot(doc => {
           setcomments(doc.data().comments)
        })
    }, [])

    const handleComment = () => {
        if (commentsRef.current.value && props.user) {
            const comment = {
                text: commentsRef.current.value,
                user: props.user.displayName,
                image: props.user.photoURL
            }
            projectFirestore.collection('posts').doc(id).update({
                comments: [...comments, comment]
            })
        }
    }

    return (
        <div>
           <div className="comments">
            {comments.map((comment) => {
                return (
                 <div className="single-comment">
                      <img src={comment.image} alt="" />                   
                    <h5>{comment.user}</h5> 
                    <h4>Commented</h4>--
                    <h4>{comment.text}</h4>
                 </div>
                )
            })}
           </div>

           <div className="comments-form">
               <input type="text" className="input-comment" ref={commentsRef}  name="" id="" placeholder="Enter your Comment"/>
               <button onClick={handleComment}>Post Comment</button>
           </div>
        </div>
    )
}

export default Comments
