import React, {useState, useEffect, useRef} from 'react'
import {projectFirestore} from '../Firebase/config'
import SearchIcon from '@material-ui/icons/Search';
import '../Styles/Search.css'
import {Link} from 'react-router-dom'


function SearchPage() {

    const searchRef = useRef()
    const [results, setresults] = useState([])
    const [filteredRes, setfilteredRes] = useState()

   projectFirestore.collection('posts').onSnapshot(snap => {
       const array = snap.docs.map(doc => {
           return doc.data()
       })
       setresults(array)
   })

   const handleSearch = () => {
   if (searchRef.current.value && setresults) {
    const searchedUser = results.find(res => {
       return res.name == searchRef.current.value
    })
     setfilteredRes(searchedUser)
     console.log(filteredRes);
   }
   }

    return (
        <div>
            <div className="search-form">
                <input ref={searchRef} className="search-input" type="search" name="" id="" placeholder="Search User" />
                <SearchIcon onClick={handleSearch} className="search-btn" />
            </div>

            <div className="search-results">
              {filteredRes && <Link to={`/userdetails/${filteredRes.id}`}><div className='searched-user'>
                  <img src={filteredRes.profile} alt="" />
                <h3>{filteredRes.name}</h3>
                  </div></Link>}
              {!filteredRes && <h1>No Users Found</h1>}
            </div>
        </div>
    )
}

export default SearchPage
