import React from 'react'
import '../Styles/NavBar.css'
import InstagramIcon from '@material-ui/icons/Instagram';
import SearchIcon from '@material-ui/icons/Search';
import AddBoxIcon from '@material-ui/icons/AddBox';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import {Link} from 'react-router-dom'


function NavBar(props) {
    return (
      <React.Fragment>
           <nav>
               <Link to='/'><div className="logo"><InstagramIcon className="nav-router" fontSize="large"/></div></Link>
               <div className="icons">
                {props.user && <Link to='/search'> <SearchIcon className="nav-router" fontSize='large'/></Link>}
               {props.user &&  <Link to='/addpost'> <AddBoxIcon className="nav-router" fontSize='large'/></Link>}
               {!props.user &&  <Link to='/aboutuser'><AccountCircleIcon className="nav-router" fontSize='large'/></Link>}
               {props.user && <Link  to='/aboutuser'><img className="nav-image"  src={props.user.photoURL} alt="" /></Link>}
               </div>
           </nav>
      </React.Fragment>
    )
}

export default NavBar
