import React, {useState, useEffect, useRef} from 'react'
import '../Styles/AddPost.css'
import {projectStorage, projectFirestore} from '../Firebase/config'
import {useHistory} from 'react-router-dom'

function AddPostPage(props) {

    const locationRef = useRef()
    const captionRef = useRef()
    const [file, setfile] = useState();
    const [fileError, setfileError] = useState(null);
    const [isPending, setisPending] = useState(true)
    const [url, seturl] = useState()
    const history = useHistory()


    const handlePost = async (e) => {
      setisPending(false)
      e.preventDefault()
     if (file && props.user) {
     await projectFirestore.collection('posts').add({
        location: locationRef.current.value,
        caption: captionRef.current.value,
        name: props.user.displayName,
        profile: props.user.photoURL,
        id: props.user.uid,
        post: url,
        comments: [],
        followers: [],
      })
     }
     setisPending(true)
     history.push('/')
    }

    const uploadFile =  async (file) => {
      setisPending(false)
    try {
     let storageRef = projectStorage.ref(`${props.user.uid}/${file.name}`)
     const data = await storageRef.put(file)
     const url = await data.ref.getDownloadURL()
     seturl(url)
     console.log(url);
     setisPending(true)
    } catch (err) {
       setisPending(true)
    }
    }



    const types = ['image/png', 'image/jpeg', 'image/jpg']
   
    const handleUploadImage = (e) => {
     let selected = e.target.files[0]

     if (selected && types.includes(selected.type) && props.user) {
       console.log(selected);
       setfile(selected)
       setfileError(null)
      uploadFile(selected)
       
     } else {
       setfile(null)
       setfileError('Unsupported Format. Try to Upload images')
     }
    }
 


    return (
        <div>
             
      <form>
        <h4>Create Your Post</h4>
        <input
          ref={locationRef}
          type="text"
          name=""
          id=""
          required
          placeholder="Your Location"
        />

        <textarea
        ref={captionRef}
          required
          placeholder="Enter Your Caption"
        ></textarea>


        <label>Upload  Image</label>
        <input type="file" onChange={handleUploadImage}  name="" id="" />
        {fileError && <div className="error">{fileError}</div>}
        {!isPending && <p>Uploading</p>}
        {!fileError && isPending &&  <button onClick={handlePost}>Create</button>}
        
      
       
      </form>
    </div>
       
    )
}

export default AddPostPage
